'use client';

import { useState, useEffect, FormEvent } from 'react';
import axios from '@/lib/axios';
import { useRouter } from 'next/navigation';
import { toast } from 'sonner';
import Link from 'next/link';
import { ArrowLeft, Save, Loader2, Calendar as CalendarIcon, CreditCard, User } from 'lucide-react';
import { format } from 'date-fns';
import { id as localeId } from 'date-fns/locale';

// UI Components
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
    Select, SelectContent, SelectItem, SelectTrigger, SelectValue,
} from "@/components/ui/select";
import {
    Popover, PopoverContent, PopoverTrigger,
} from "@/components/ui/popover";
import { Calendar } from '@/components/ui/calendar';
import { cn } from '@/lib/utils';

// Types (Import dari types global)
import { Lapangan, PaymentMethod, User as UserType, StatusBooking } from '@/types';

export default function AdminCreateBookingPage() {
    const router = useRouter();
    const [isSaving, setIsSaving] = useState(false);

    // Data Master
    const [lapangans, setLapangans] = useState<Lapangan[]>([]);
    const [users, setUsers] = useState<UserType[]>([]);
    const [paymentMethods, setPaymentMethods] = useState<PaymentMethod[]>([]);
    const [statuses, setStatuses] = useState<StatusBooking[]>([]);

    // Form Data (Disesuaikan dengan Schema)
    const [formData, setFormData] = useState({
        user_id: '',
        lapangan_id: '',
        tanggal_booking: undefined as Date | undefined,
        jam_mulai: '',
        durasi_jam: '1',
        payment_method_id: '',
        status_booking_id: '1', // Default: Menunggu Pembayaran
        acara: '',              // Nama Club / Kegiatan
        asal_bank: '',          // New: Asal Bank
        nama_pengirim: '',      // New: Nama Pengirim
        jumlah_dp: '',          // Uang Muka / Bayar
    });

    // Fetch Data Master
    useEffect(() => {
        const initData = async () => {
            try {
                const [resLap, resUser, resPay, resStatus] = await Promise.all([
                    axios.get('/api/lapangans'),
                    axios.get('/api/users'), 
                    axios.get('/api/paymentMethods'),
                    axios.get('/api/statusBookings'),
                ]);
                setLapangans(resLap.data);
                setUsers(resUser.data.filter((u: any) => u.role_id === 3)); 
                setPaymentMethods(resPay.data);
                setStatuses(resStatus.data);
            } catch (error) {
                console.error(error);
                toast.error("Gagal memuat data master.");
            }
        };
        initData();
    }, []);

    // Helper: Hitung Jam Selesai & Total Harga
    const getJamSelesai = () => {
        if (!formData.jam_mulai) return '--:--';
        const [hours, minutes] = formData.jam_mulai.split(':').map(Number);
        const endHours = hours + Number(formData.durasi_jam);
        return `${String(endHours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
    };

    const getEstimasiHarga = () => {
        if (!formData.lapangan_id) return 0;
        const lapangan = lapangans.find(l => l.id.toString() === formData.lapangan_id);
        return lapangan ? Number(lapangan.harga_per_jam) * Number(formData.durasi_jam) : 0;
    };

    // Submit Handler
    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        
        if (!formData.user_id || !formData.lapangan_id || !formData.tanggal_booking || !formData.jam_mulai || !formData.payment_method_id) {
            return toast.error("Mohon lengkapi semua field wajib (*)!");
        }

        setIsSaving(true);
        try {
            // 1. Buat Booking
            const bookingPayload = {
                user_id: parseInt(formData.user_id), 
                lapangan_id: parseInt(formData.lapangan_id),
                tanggal_booking: format(formData.tanggal_booking!, 'yyyy-MM-dd'),
                jam_mulai: formData.jam_mulai,
                durasi_jam: parseInt(formData.durasi_jam),
                payment_method_id: parseInt(formData.payment_method_id),
                acara: formData.acara,
                asal_bank: formData.asal_bank,     // Field Baru
                nama_pengirim: formData.nama_pengirim, // Field Baru
            };

            const resBooking = await axios.post('/api/bookings', bookingPayload);
            const bookingId = resBooking.data.id;

            // 2. Update Status Langsung (Privilege Admin)
            if (formData.status_booking_id !== '1') {
                await axios.patch(`/api/bookings/${bookingId}/status`, {
                    status_booking_id: parseInt(formData.status_booking_id)
                });
            }

            toast.success("Booking berhasil dibuat!");
            router.push('/admin/bookings');

        } catch (error: any) {
            console.error(error);
            const msg = error.response?.data?.message || "Gagal membuat booking.";
            
            if (error.response?.status === 422 && error.response?.data?.errors?.jam_mulai) {
                 toast.error("Jadwal Bentrok!", { description: "Jam yang dipilih sudah terisi." });
            } else {
                toast.error(msg);
            }
        } finally {
            setIsSaving(false);
        }
    };

    return (
        <div className="space-y-6 pb-20 max-w-4xl mx-auto">
            {/* Header */}
            <div className="flex items-center gap-4">
                <Link href="/admin/bookings">
                    <Button variant="outline" size="icon"><ArrowLeft className="h-4 w-4" /></Button>
                </Link>
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Input Booking Manual</h1>
                    <p className="text-gray-500 text-sm">Catat penyewaan lapangan untuk pelanggan offline/telepon.</p>
                </div>
            </div>

            <form onSubmit={handleSubmit} className="space-y-6">
                
                <Card>
                    <CardHeader className="pb-3 border-b mb-4">
                        <CardTitle className="text-base flex items-center gap-2">
                            <User className="w-4 h-4" /> Informasi Penyewa
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        <div className="space-y-2">
                            <Label>Pilih Pelanggan <span className="text-red-500">*</span></Label>
                            <Select onValueChange={(v) => setFormData({...formData, user_id: v})}>
                                <SelectTrigger>
                                    <SelectValue placeholder="Cari nama pelanggan..." />
                                </SelectTrigger>
                                <SelectContent>
                                    {users.map(u => (
                                        <SelectItem key={u.id} value={u.id.toString()}>{u.name} ({u.nomor_telepon || '-'})</SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                            <p className="text-xs text-gray-500">
                                Belum terdaftar? <Link href="/admin/users" className="text-blue-600 hover:underline">Tambah User Baru</Link> dulu.
                            </p>
                        </div>
                        <div className="space-y-2">
                            <Label>Nama Club / Kegiatan</Label>
                            <Input 
                                placeholder="Cth: Latihan Rutin, Turnamen Kecil" 
                                value={formData.acara} 
                                onChange={e => setFormData({...formData, acara: e.target.value})} 
                            />
                        </div>
                    </CardContent>
                </Card>

                <Card>
                    <CardHeader className="pb-3 border-b mb-4">
                        <CardTitle className="text-base flex items-center gap-2">
                            <CalendarIcon className="w-4 h-4" /> Jadwal Main
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div className="space-y-2">
                                <Label>Pilih Lapangan <span className="text-red-500">*</span></Label>
                                <Select onValueChange={(v) => setFormData({...formData, lapangan_id: v})}>
                                    <SelectTrigger><SelectValue placeholder="Pilih Lapangan" /></SelectTrigger>
                                    <SelectContent>
                                        {lapangans.map(l => (
                                            <SelectItem key={l.id} value={l.id.toString()}>{l.nama_lapangan}</SelectItem>
                                        ))}
                                    </SelectContent>
                                </Select>
                            </div>
                            
                            <div className="space-y-2">
                                <Label>Tanggal Main <span className="text-red-500">*</span></Label>
                                <Popover>
                                    <PopoverTrigger asChild>
                                        <Button
                                            variant={"outline"}
                                            className={cn(
                                                "w-full justify-start text-left font-normal",
                                                !formData.tanggal_booking && "text-muted-foreground"
                                            )}
                                        >
                                            <CalendarIcon className="mr-2 h-4 w-4" />
                                            {formData.tanggal_booking ? format(formData.tanggal_booking, "PPP", { locale: localeId }) : <span>Pilih tanggal</span>}
                                        </Button>
                                    </PopoverTrigger>
                                    <PopoverContent className="w-auto p-0" align="start">
                                        <Calendar
                                            mode="single"
                                            selected={formData.tanggal_booking}
                                            onSelect={(date) => setFormData({...formData, tanggal_booking: date})}
                                            initialFocus
                                        />
                                    </PopoverContent>
                                </Popover>
                            </div>
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                            <div className="space-y-2">
                                <Label>Jam Mulai <span className="text-red-500">*</span></Label>
                                <Input type="time" value={formData.jam_mulai} onChange={e => setFormData({...formData, jam_mulai: e.target.value})} />
                            </div>
                            <div className="space-y-2">
                                <Label>Durasi (Jam)</Label>
                                <Input type="number" min="1" value={formData.durasi_jam} onChange={e => setFormData({...formData, durasi_jam: e.target.value})} />
                            </div>
                            <div className="space-y-2">
                                <Label>Jam Selesai (Est)</Label>
                                <Input value={getJamSelesai()} disabled className="bg-gray-100" />
                            </div>
                        </div>
                    </CardContent>
                </Card>

                <Card>
                    <CardHeader className="pb-3 border-b mb-4">
                        <CardTitle className="text-base flex items-center gap-2">
                            <CreditCard className="w-4 h-4" /> Pembayaran
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        <div className="p-4 bg-gray-50 border rounded-lg flex justify-between items-center">
                            <span className="text-sm font-medium">Total Harga Sewa:</span>
                            <span className="text-xl font-bold text-[#D93F21]">
                                {new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR' }).format(getEstimasiHarga())}
                            </span>
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div className="space-y-2">
                                <Label>Metode Pembayaran <span className="text-red-500">*</span></Label>
                                <Select onValueChange={(v) => setFormData({...formData, payment_method_id: v})}>
                                    <SelectTrigger><SelectValue placeholder="Pilih Metode" /></SelectTrigger>
                                    <SelectContent>
                                        {paymentMethods.map(p => (
                                            <SelectItem key={p.id} value={p.id.toString()}>{p.nama_metode}</SelectItem>
                                        ))}
                                    </SelectContent>
                                </Select>
                            </div>

                            <div className="space-y-2">
                                <Label>Status Awal</Label>
                                <Select 
                                    defaultValue="1" 
                                    onValueChange={(v) => setFormData({...formData, status_booking_id: v})}
                                >
                                    <SelectTrigger><SelectValue placeholder="Pilih Status" /></SelectTrigger>
                                    <SelectContent>
                                        {statuses.map(s => (
                                            <SelectItem key={s.id} value={s.id.toString()}>{s.nama_status}</SelectItem>
                                        ))}
                                    </SelectContent>
                                </Select>
                                <p className="text-xs text-gray-500">
                                    Pilih "Terkonfirmasi" jika pelanggan sudah bayar lunas di kasir.
                                </p>
                            </div>
                        </div>
                        
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                             <div className="space-y-2">
                                 <Label>Asal Bank (Opsional)</Label>
                                 <Input 
                                    placeholder="Cth: Cash / Transfer BCA"
                                    value={formData.asal_bank}
                                    onChange={e => setFormData({...formData, asal_bank: e.target.value})}
                                 />
                             </div>
                             <div className="space-y-2">
                                 <Label>Nama Pengirim (Opsional)</Label>
                                 <Input 
                                    placeholder="Nama pembayar"
                                    value={formData.nama_pengirim}
                                    onChange={e => setFormData({...formData, nama_pengirim: e.target.value})}
                                 />
                             </div>
                        </div>
                    </CardContent>
                </Card>

                <div className="flex justify-end gap-4">
                    <Link href="/admin/bookings">
                        <Button variant="outline" type="button">Batal</Button>
                    </Link>
                    <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b] px-8">
                        {isSaving ? <Loader2 className="h-4 w-4 animate-spin mr-2" /> : <Save className="h-4 w-4 mr-2" />}
                        Simpan Booking
                    </Button>
                </div>

            </form>
        </div>
    );
}